	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"For an industry steeped in a mythos of innovation and invention, the path towards reliable software is often obstructed by a web of fixed ideas. Fixed ideas, by actively obscuring alternatives, lead us to inaccurately the weigh the risks and benefits associated with our choices whether they be of an engineering, managerial or business nature. Alan Kay famously quipped that a difference in perspective is worth 80 IQ points but in this talk we'll see that it's worth considerably more than that.","meta":{"duration":"PT39M37S"},"presenter":{"name":"David Nolen"},"source":{"id":"lzXHMy4ewtM","provider":"youtube"},"slug":"idee-fixe","tag":["GOTO Conference","Chicago","Keynote","Innovation"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/lzXHMy4ewtM/default.jpg","height":90,"width":120}},"title":"Idée Fixe"}}]}},"pathContext":{"slug":"innovation","tag":"Innovation"}}

